// Variables globals CSS
:root {
  // Colors
  --color-text: #f4f2f2;
  --color-text-dark: #3d3a3a;
  --color-white: #ffffff;
  --color-black: #202221;
  --color-orange: ##ff6f50;

  // Fons
  --color-bg-body: #fffafa;
  --color-bg-black: #202221;
  --color-bg-black-semitransparent: #20222118;

  // Gradients
  --gradient-destacat: linear-gradient(
    to right,
    var(--color-bg-black) 50%,
    var(--color-bg-black-semitransparent) 100%
  );

  // Tipografia
  --font-size-base: 16px;
  --font-family-title: 'ABeeZee', sans-serif;
  --font-family-body: 'Inter', sans-serif;
  --font-family-card: 'DM Sans', sans-serif;

  // Spacing

  // Base 8px (genera --base-2 fins a --base-20, amb 0.5rem (8px) com a base)
  --base-1: 0.5rem;
  @for $i from 2 to 20 {
    --base-#{$i}: calc(var(--base-1) *#{$i});
  }

  // 1170px de mida màxima del body per mides de pantalla grans
  // 1170px + 1.5rem (padding extern)
  --boxed: calc(73.125rem + 1.875rem);

  // Z-index
  --z-index-p1: 1;
  --z-index-p2: 2;
  --z-index-1: -1;
  --z-index-2: -2;

  // Rutes
  --icon-google: url('../cow/assets/svg/icon-google-colors.svg');
}

